}
.c-button {
    padding: var(--cf-scale-2) var(--cf-scale-5);
    font-size: var(--cf-scale-2);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-weight: 500;
    line-height: 2;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    border-radius: var(--cf-scale-2);
    transition: background 250ms ease, box-shadow 250ms ease, opacity 250ms ease;
    vertical-align: middle;
    border: 0;
    font-family: "Sohne", "Helvetica", "Arial", sans-serif;
}

