.cf-breadcrumb a,.cf-breadcrumb a:hover,.cf-breadcrumb a:focus,.cf-header__login-link,.cf-header__login-link:hover,.cf-header__login-link:focus {
    color: #fff;
    text-decoration: none
}

.mobile-nav-active {
    position: fixed;
    overflow: hidden
}

.mobile-nav-active .cf-header__sign-out {
    display: block
}

.cf-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    z-index: 120
}

.cf-header:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: #000;
    z-index: -1
}

.cf-header:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 300px;
    left: 0;
    right: 0;
    top: -300px;
    background: #000;
    z-index: 9999
}

.cf-header .cf-container {
    padding: 0
}

.cf-header__sign-out {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    font-size: 17px;
    letter-spacing: 1px;
    text-align: center;
    background: rgba(0,0,0,.9);
    color: rgba(255,255,255,.5);
    padding: 32px 16px;
    z-index: 120
}

.cf-header__inner {
    position: relative;
    padding: 11px 10px;
    text-transform: uppercase;
    max-width: 1200px;
    margin: 0 auto
}

.cf-header__dropdowns {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 130
}

.cf-header__right-col {
    display: flex;
    height: 34px;
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    padding: 0 10px
}

.cf-header__logo,.cf-header .cf-breadcrumb {
    display: none
}

.cf-header__wordmark {
    position: relative;
    z-index: 1;
    display: block;
    margin: 0 auto;
    width: 124px
}

.cf-header__wordmark img {
    display: block;
    width: 100%;
    max-width: 100%
}

.cf-header__login-link {
    position: relative;
    display: inline-block;
    margin-top: 6px;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 1.5px;
    line-height: 1.6
}

.cf-header__login-link:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -0.5em;
    left: auto;
    width: 0;
    height: 2px;
    background: #f24462;
    transition: width 250ms ease
}

.cf-header.logged-out .cf-header__wordmark {
    margin: 0
}

.cf-header.logged-out .cf-header__inner {
    padding: 17px 17px 18px
}

.cf-header.logged-out .cf-header__right-col {
    left: auto;
    height: 48px;
    padding: 7px 17px 0
}

.cf-header .mobile-only .avatar {
    margin-right: 1em
}

.cf-header .desktop-only .avatar {
    margin-left: 1em
}

.cf-header .comment-content {
    display: block;
    position: relative;
    padding-left: calc(1em + 45px)
}

.cf-header .comment-content .chapter-marker {
    margin-bottom: .5em
}

.cf-header .comment-content .user {
    font-weight: bold;
    margin-right: .25em;
    color: #fff
}

.cf-header .comment-content .timestamp {
    text-transform: uppercase;
    font-size: .8em;
    margin-top: 1em;
    color: rgba(255,255,255,.5)
}

@media(min-width: 768px) {
    .mobile-nav-active {
        position:static;
        overflow: auto
    }

    .mobile-nav-active .cf-header__sign-out {
        display: none
    }

    .cf-header {
        max-height: 90px;
        transition: background 450ms cubic-bezier(0.25, 0.1, 0.25, 1),max-height 450ms cubic-bezier(0.25, 0.1, 0.25, 1)
    }

    .cf-header__right-col {
        left: auto;
        padding: 0 10px;
        height: 34px
    }

    .cf-header__dropdowns {
        z-index: auto
    }

    .cf-header__logo {
        position: relative;
        display: block;
        width: 124px;
        height: 56px;
        margin: 0 auto;
        transition: margin 450ms cubic-bezier(0.25, 0.1, 0.25, 1)
    }

    .cf-header__logo img {
        width: 100%;
        height: auto
    }

    .cf-header__wordmark {
        margin: 0 auto;
        float: none
    }

    .cf-header .cf-breadcrumb {
        display: block;
        position: absolute;
        top: 11px;
        left: 10px;
        display: block;
        font-size: .75em;
        font-weight: 600;
        line-height: 1;
        letter-spacing: .0833em
    }

    .cf-header .cf-breadcrumb:before {
        content: "<";
        display: inline-block;
        vertical-align: middle;
        margin-right: .5em;
        color: #f24462
    }

    .no-touch .cf-header__login-link:hover:after {
        left: 0;
        right: auto;
        width: 100%
    }

    .cf-header.logged-out {
        background: linear-gradient(to bottom, black 0%, rgba(0, 0, 0, 0) 100%);
        background-repeat: repeat-x;
        background-position: 0 -50px
    }

    .cf-header.logged-out .cf-header__inner {
        padding: 11px 10px
    }

    .cf-header.logged-out .cf-header__right-col {
        padding: 0 10px
    }

    .cf-header.logged-out:before {
        opacity: 0;
        transition: opacity 450ms cubic-bezier(0.25, 0.1, 0.25, 1)
    }

    .cf-header.condensed {
        max-height: 34px
    }

    .cf-header.condensed:before {
        opacity: 1
    }

    .cf-header.condensed .cf-container:not(.cm-sticky-ctas) {
        background: #000;
        transition: background 0ms 450ms ease
    }

    .cf-header.condensed #logo-primary {
        margin-top: -44px;
        transition: margin 450ms cubic-bezier(0.25, 0.1, 0.25, 1)
    }

    .cf-nav-class-link {
        position: absolute;
        top: 8px;
        left: 8px;
        font-size: 12px;
        font-weight: bold;
        letter-spacing: 1.5px
    }

    .cf-nav-class-link .cf-arrow-thin-red--left {
        margin-right: 7px
    }

    .cf-nav-class-link .cf-arrow-thin-red--right {
        margin: 0 7px
    }

    .cf-nav-class-link.breadcrumbs>* {
        display: inline-block;
        vertical-align: middle
    }

    .cf-nav-class-link.breadcrumbs a {
        color: #fff;
        transition: color 250ms ease
    }

    .cf-nav-class-link.breadcrumbs a:hover {
        color: rgba(255,255,255,.75)
    }

    .cf-nav-class-link.breadcrumbs .instructor-name {
        color: #fff
    }

    .cf-header.logged-out {
        padding: 0
    }

    .cf-header.logged-out #logo-wordmark {
        display: none
    }

    .cf-header.nav-open {
        overflow: visible
    }

    .cf-header.nav-open .cf-nav-items {
        margin-top: 0
    }

    .cf-header .cf-nav-items {
        display: block;
        position: absolute;
        top: .45em;
        right: .5em;
        overflow: visible
    }

    .cf-header .cf-nav-items .cf-nav-item {
        display: inline-block;
        vertical-align: middle;
        margin: 0 .4em
    }

    .cf-header .cf-nav-items .cf-nav-item.comments .comments-dropdown {
        display: block
    }

    .cf-header .cf-nav-items .cf-nav-item.comments .comments-dropdown.unread:after {
        content: "";
        position: absolute;
        top: .8em;
        right: -0.1em;
        display: block;
        width: 6px;
        height: 6px;
        background: #f24462;
        border-radius: 6px
    }

    .cf-header .cf-nav-items .cf-nav-item.comments .cf-subnav-item .comment {
        color: rgba(89,97,112,.8)
    }

    .cf-header .cf-nav-items .cf-nav-item.cart {
        padding: 0
    }

    .cf-header .cf-subnav-items {
        position: absolute;
        top: 3.5em;
        right: -16px;
        margin-top: 0;
        width: 350px;
        background: #fff;
        color: #596170;
        opacity: 0;
        visibility: hidden
    }

    .cf-header .cf-subnav-items:before {
        content: "";
        display: block;
        position: absolute;
        top: -30px;
        right: 10px;
        height: 15px;
        border: 15px solid transparent;
        border-bottom: 15px solid #fff
    }

    .cf-header .cf-subnav-items:after {
        content: "";
        display: block;
        position: absolute;
        top: -24px;
        left: 0;
        width: 100%;
        height: 24px;
        background: none
    }

    .cf-header .cf-subnav-item {
        border-bottom: 1px solid rgba(0,0,0,.1)
    }

    .cf-header .cf-subnav-item:first-child {
        border-top: 0
    }

    .cf-header .cf-subnav-item a {
        color: rgba(89,97,112,.8);
        transition: color 150ms ease
    }

    .cf-header .cf-subnav-item .cf-btn.clear {
        color: #596170;
        border: 1px solid rgba(0,0,0,.5);
        margin-bottom: 0;
        transition: border 250ms ease,color 250ms ease
    }

    .cf-header .cf-subnav-item .cf-btn.clear:hover {
        color: rgba(89,97,112,.8);
        border: 1px solid rgba(0,0,0,.25)
    }

    .cf-header .comment-content .user {
        color: #596170
    }

    .cf-header .comment-content .timestamp {
        color: rgba(89,97,112,.5)
    }
}

@media(min-width: 992px) {
    #cf-header .class-switcher li {
        margin-right:4em
    }
}

.mobile-nav-button {
    position: absolute;
    top: 6px;
    right: 10px;
    width: 22px;
    height: 22px;
    z-index: 30
}

.mobile-nav-button__lines {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background: #fff
}

.mobile-nav-button__lines:before,.mobile-nav-button__lines:after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background: #fff;
    transform-origin: center center;
    transition: top 125ms ease 125ms,transform 125ms ease 0ms
}

.mobile-nav-button__lines:before {
    top: -6px
}

.mobile-nav-button__lines:after {
    top: 6px
}

.nav-open .mobile-nav-button__lines {
    top: 50%;
    left: 0;
    background: none;
    transition: background 0ms 125ms ease
}

.nav-open .mobile-nav-button__lines:before,.nav-open .mobile-nav-button__lines:after {
    transition: top 125ms ease,transform 125ms ease 125ms
}

.nav-open .mobile-nav-button__lines:before {
    top: 50%;
    transform: rotate(-45deg)
}

.nav-open .mobile-nav-button__lines:after {
    top: 50%;
    transform: rotate(45deg)
}

.cf-btn {
    background: #f24462;
    display: inline-block;
    vertical-align: middle;
    font-size: .9em;
    font-weight: normal;
    text-transform: uppercase;
    margin: .55em;
    padding: 10px 20px;
    border: 0;
    border-radius: 4px;
    letter-spacing: .05em;
    color: #fff;
    transition: background 250ms ease,color 250ms ease,border 250ms ease
}

.cf-btn:hover,.cf-btn:focus {
    background: #f45c76;
    color: #fff;
    text-decoration: none
}

.cf-btn.no-margins {
    margin: 0
}

.cf-btn.cf-btn-transparent {
    background: rgba(255,255,255,0);
    border: 1px solid rgba(255,255,255,.5)
}

.cf-btn.cf-btn-transparent:hover {
    background: rgba(255,255,255,.1)
}

.cf-btn.cf-btn-transparent.invert {
    background: rgba(0,0,0,0);
    border: 1px solid rgba(89,97,112,.2);
    color: rgba(0,0,0,.6)
}

