.react-tabs {
    margin-top: 12px
}

.react-tabs--no-margin {
    margin-top: 0px
}

.react-tabs__tab-list {
    display: flex;
    border-bottom: 1px solid #ccc;
    padding: 0 16px
}

.react-tabs__tab {
    position: relative;
    flex: 1;
    display: inline-block;
    max-width: 50%;
    border: 1px solid transparent;
    border-bottom: none;
    bottom: -1px;
    position: relative;
    list-style: none;
    padding: 12px;
    padding-top: 14px;
    border-color: #ccc;
    border-radius: 2px 2px 0 0;
    text-align: center;
    color: rgba(77,87,104,.7);
    text-transform: uppercase;
    letter-spacing: .03em;
    font-weight: 600;
    cursor: pointer
}

.react-tabs__tab--selected {
    background: #fff;
    color: #4d5768
}

.react-tabs__tab--selected:before {
    content: "";
    position: absolute;
    z-index: 1;
    top: -1px;
    left: -1px;
    right: -1px;
    height: 3px;
    border-radius: 2px 2px 0 0;
    background-color: #be3f3f
}

.react-tabs__tab--disabled {
    display: none
}

.react-tabs__tab:not(:last-child) {
    margin-right: 6px
}

.react-tabs__tab:focus {
    outline: none
}

.react-tabs__tab:focus:after {
    content: "";
    position: absolute;
    height: 5px;
    left: -4px;
    right: -4px;
    bottom: -5px;
    background: #fff
}

.react-tabs__tab-panel {
    display: none;
    padding: 16px
}

.react-tabs__tab-panel--selected {
    display: block
}
